import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    /* Home */
    client: {},
    clientAll: [],
    /*  Sections */
    section: {
      tab: "Tableros",
      content: "BoardList",
      template: [
      ],
      data: [
      ],
    },
    sectionAll: [
      /* {
        tab: "ACOMETIDA",
        content: "UndefinedTemporary",
      }, */
      {
        tab: "Tableros",
        content: "BoardList",
        template: [],
        data: [
          {
            reference: 1,
            nomenclature: "",
            location: "",
          },
        ],
      },

      {
        tab: "CONTACTOS",
        content: "ContactList",
      },

      {
        tab: "AREAS",
        content: "AreaList",
      },
      {
        tab: "TIERRAS",
        content: "UndefinedTemporary",
      },



      

    ],
    /* Boards */
    board: {},
    boardAll: [],
    /* Contacts */
    contact: {},
    contactAll: [],

    /* Areas*/
    area: {},
    areaAll: [],
  },
  getters: {
    /* Home */
    getClient(state) {
      state.boardAll = [];
      return state.client;
    },
    getClientAll(state) {
      return state.clientAll;
    },
    /*  Sections */
    getSection(state) {
      return state.sectionAll;
    },
    /* Boards */
    getBoard(state) {
      return state.board;
    },
    getBoardAll(state) {
      return state.boardAll;
    },
    /* Contacts */
    getContact(state) {
      return state.contact;
    },
    getContactAll(state) {
      return state.contactAll;
    },
    /* Areas */
    getArea(state) {
      return state.area;
    },
    getAreaAll(state) {
      return state.areaAll;
    }
  },
  mutations: {
    /* Home */
    newClient(state, dt) {
      state.clientAll.unshift(dt);
      state.boardAll = [];
    },
    setClient(state, dt) {
      state.client = dt;
    },
    updateClient(state, dt) {
      console.log(dt);

      for (const obj of state.clientAll) {
        if (obj.id === dt.id) {
          console.log('mach');
          obj.tradename = dt.tradename;
          obj.municipality = dt.municipality;
          obj.date = dt.date;
          obj.whoAttendedUs = dt.whoAttendedUs;
          obj.typeOfOpinion = dt.typeOfOpinion;

          break;
        }
      }
      console.log(state.clientAll);
    },
    deleteClientAll(state) {
      state.clientAll = [];
    },
    /* Section */
    setSectionAll(state, dt) {
      console.log(dt);
      state.boardAll = [];
      state.contactAll = [];
      state.areaAll = [];
      //Borrar datos anteriores para poder tener un buen orden.. es temporal..

      state.sectionAll = dt;
    },
    /*  Board */
    newBoard(state, dt) {
      state.boardAll.push(dt);
    },

    /*  Contact*/
    newContact(state, dt) {
      state.contactAll.push(dt);
    },
    /*  Area*/
    newArea(state, dt) {
      state.areaAll.push(dt);
    },


  },
  actions: {
    /* Home */
    newClientAction(context, dt) {
      context.commit('newClient', dt);
    },
    setClientAction(context, dt) {
      context.commit('setClient', dt);
    },
    updateClientAction(context, dt) {
      context.commit('updateClient', dt);

    },
    deleteClientAllAction(context) {
      context.commit('deleteClientAll');
    },
    setSectionAllAction(context, dt) {
      context.commit('setSectionAll', dt);
    },

    /* Board */
    newBoardAction(context, dt) {
      context.commit('newBoard', dt);
    },
    /* Contact */
    newContactAction(context, dt) {
      context.commit('newContact', dt);
    },

    /* Area */
    newAreaAction(context, dt) {
      context.commit('newArea', dt);
    },


    // Contact
  },
  modules: {}
})
